<template>
  <div>
    <h3>Total USD Volume Processed: ${{ Number(config.totalUsd).toLocaleString() }}</h3>
    <h3>Uptime: {{ duration }} days</h3>
    <h3>Last time events parsed: {{ lastTime }}</h3>
    <h3>Total number of tokens used multisender: {{ data.length }}</h3>
    <div class="all-token">
      <div class="all-token__inner">
        <div class="all-token__item all-token__item--header">
          <div class="all-token__cell all-token__cell--name all-token__cell--header">
            Token Name
          </div>
          <div class="all-token__cell all-token__cell--name all-token__cell--header">
            Token Symbol
          </div>
          <div class="all-token__cell all-token__cell--address all-token__cell--header">
            Address
          </div>
          <div class="all-token__cell all-token__cell--token all-token__cell--header">
            Total Amount
          </div>
          <div class="all-token__cell all-token__cell--token all-token__cell--header">
            Total Amount in USD
          </div>
        </div>
        <div v-for="(item, index) in data" :key="index" class="all-token__item">
          <div class="all-token__cell all-token__cell--name">
            <span class="all-token__help-name">Token Name: </span> {{ item[1].name }}
          </div>
          <div class="all-token__cell all-token__cell--symbol">
            <span class="all-token__help-name">Token Symbol: </span> {{ item[1].symbol }}
          </div>
          <div class="all-token__cell all-token__cell--address">
            <span class="all-token__help-name">Address:</span>
            <a target="_blank" :href="explorerBaseUrl(item[0])">{{ toUrlAddress(item[0]) }}</a>
          </div>
          <div class="all-token__cell all-token__cell--amount">
            {{ item[1].valueWithDecimals }} {{ item[1].symbol }}
          </div>
          <div class="all-token__cell all-token__cell--token">
            <span v-if="item[1].usdValue">$</span>{{ item[1].usdValue }}
          </div>
        </div>
      </div>
      <div class="wrap-image wrap-image--bg">
        <img src="~assets/img/token.png" alt="token" class="wrap-image__images" />
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable no-console */
import moment from 'moment'
import { mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters('metamask', ['networkConfig']),
    ...mapGetters('token', ['toDecimals']),
    duration() {
      const currentTime = moment()
      const then = moment(1519769902000)
      return currentTime.diff(then, 'days')
    },
    lastTime() {
      return moment(this.config.lastUpdated).format('LLLL')
    }
  },
  async asyncData({ $axios }) {
    const data = await $axios.$get(
      'https://raw.githubusercontent.com/peppersec/multisender_statistics/master/multisended_stats.json'
    )

    const config = await $axios.$get(
      'https://raw.githubusercontent.com/peppersec/multisender_statistics/master/statisticsConfig.json'
    )
    console.log(config)
    return { data, config }
  },
  methods: {
    toUrlAddress(item) {
      if (item === '0x000000000000000000000000000000000000bEEF') {
        return 'Ether'
      } else {
        return item
      }
    },
    explorerBaseUrl(item) {
      if (process.client) {
        const url = new window.URL(this.networkConfig.explorerUrl.tx).origin
        return `${url}/address/${this.toUrlAddress(item)}`
      }
      const url = this.networkConfig.explorerUrl.tx.slice(0, -4)
      return `${url}/address/${this.toUrlAddress(item)}`
    }
  }
}
</script>
